import {Outlet} from "react-router-dom";
import MenuLink from "./MenuLink";
import Styles from "./appbar.module.css"
import {faComments, faHouse, faPlus, faUserGear, faUsers, faWandMagicSparkles} from '@fortawesome/free-solid-svg-icons'


const Appbar = () => {
    return (
        <>
            <div className={Styles.navWrapper}>
                <nav className={Styles.nav}>
                    <MenuLink iconName={faHouse} to="/"></MenuLink>
                    <MenuLink iconName={faPlus} to="/Services"></MenuLink>
                    <MenuLink iconName={faWandMagicSparkles} to="/challenge"></MenuLink>
                    <MenuLink iconName={faUserGear} to="/profile"></MenuLink>
                    <MenuLink iconName={faComments} to="/chat"></MenuLink>
                </nav>
            </div>
            <Outlet/>
        </>
    );
};

export default Appbar;